import {
  fromCognitoIdentityPool,
} from '@aws-sdk/credential-provider-cognito-identity';

import { S3Client } from '@aws-sdk/client-s3';
import { Upload } from '@aws-sdk/lib-storage'

// import * as AWS from "@aws-sdk/client-s3";
// const client = new AWS.S3({ region: "us-east-1" });

export const getS3Client = async (idToken: string) => {
  const credentials = fromCognitoIdentityPool({
    identityPoolId: import.meta.env.PUB_IDENTITY_POOL_ID,
    clientConfig: { region: 'us-east-1' },
    logins: {
      [import.meta.env.PUB_COGNITO_REALM.replace('https://', '')]: idToken,
    },
  });

  return new S3Client({
    region: 'us-east-1',
    credentials,
  });
};

export const uploadFileToS3 = async (bucket: string, key: string, file: File, s3Client: S3Client) => {
  const upload = new Upload({
    client: s3Client,
    params: {
      Bucket: bucket,
      Key: key,
      Body: file,
      ContentType: file.type,
    },
  });

  try {
    const result = await upload.done();
    return result;
  } catch (err) {
    console.error('❌ Upload failed:', err);
    return false;
  }
};