import cn from 'classnames';
import { Modal } from '@/components/ui/Modal';
import { Radio } from '@/components/ui/Radio';

import { Button } from '@/components/ui/Button';
import styles from './styles.module.css';
import boardStyles from '../board.styles.module.css';
import { Avatar } from '@/components/ui/Avatar';
import { Input } from '@/components/ui/Input';
import { List } from '@/components/ui/List';
import { Item } from '@/components/ui/List/Item';
import { useRef, useState } from 'react';
import { useAppContext } from '@/utils';
import { updateUserAttributes, updateUserPassword } from '@/utils/profile';
import { useAuth } from 'react-oidc-context';
import { getS3Client, uploadFileToS3 } from '@/utils/s3';

export const Account = () => {
  const { state, } = useAppContext();
  const auth = useAuth();
  const { user } = state;
  console.log("user:", user)

  const [avatarUrl, setAvatarUrl] = useState(
    user?.['custom:pictureUrl'] ? user?.['custom:pictureUrl']  + '?' + new Date().getTime() : ''
  );

  const avatarFileUploadRef = useRef<HTMLInputElement>(null);
  const userNameRef = useRef<HTMLInputElement>(null);
  const userEmailRef = useRef<HTMLInputElement>(null);
  const userPwdOld = useRef<HTMLInputElement>(null);
  const userPwdNew1 = useRef<HTMLInputElement>(null);
  const userPwdNew2 = useRef<HTMLInputElement>(null);

  // const userAttributes = user?.id_token ? JSON.parse(atob(user?.id_token.split(".")[1])) : {};

  const [isTwoFactorAuthModalShown, showTwoFactorAuthModal] = useState(false);
  const [isRemoveAccModalShown, showRemoveAccModal] = useState(false);
  const [isEditAccountInfo, setEditAccountInfo] = useState(false);
  const [isChangePassModalShown, showChangePassModal] = useState(false);
  const [isChangePassEdit, setChangePassEdit] = useState(false);

  const saveAccountChanges = async () => {
    const update = {
      'email': userEmailRef.current?.value || '',
      'name': userNameRef.current?.value || '',
    };
    const result = await updateUserAttributes(update);
    setEditAccountInfo(false);
    if (result) {
      try {
        await auth.signinSilent(); // This refreshes the tokens
        console.log("User session refreshed:", auth.user);
      } catch (error) {
        console.error("Error refreshing session", error);
      }
    }
  }

  const saveUserPassword = async () => {
    const pwdold = userPwdOld.current?.value || ''
    const pwdnew1 = userPwdNew1.current?.value || '';
    const pwdnew2 = userPwdNew2.current?.value || '';
    if (pwdnew1 === pwdnew2) {
      const update = await updateUserPassword(pwdold, pwdnew2);
      if (update) {
        setChangePassEdit(false);
      }
    }
  }

  const uploadFile = async function (file?: File) {
    if (file && user?.sub) {
      const client = await getS3Client(user.id_token || '');
      const result = await uploadFileToS3(
        import.meta.env.PUB_BUCKET_AVATARS, 'avatars/' + user.sub, file, client
      );
      const location = result && result.Location || '';
      if (location) {
        console.log('image location', location)
        const update = {
          'custom:pictureUrl': location,
        };
        setAvatarUrl(location);
        await updateUserAttributes(update);
      }
    }
  }

  return (
    <div className={styles.block}>
      {/* Account */}
      <div className={boardStyles.subheader}>
        <span className={boardStyles.subheading}>Account details</span>
        {!isEditAccountInfo && (
          <Button
            onClick={() => setEditAccountInfo(true)}
            className={boardStyles.editButton}
          >
            Edit information
          </Button>
        )}
      </div>
      {!isEditAccountInfo && (
        <table className={styles.table}>
          <tbody>
            <tr>
              <td className={styles.label}>Avatar</td>
              <td>
                <Avatar image={avatarUrl} initials="UN" />
              </td>
            </tr>
            <tr>
              <td className={styles.label}>Email</td>
              <td>{user?.email}</td>
            </tr>
            <tr>
              <td className={styles.label}>Name</td>
              <td>{user?.name || 'not set'}</td>
            </tr>
          </tbody>
        </table>
      )}
      {isEditAccountInfo && (
        <div>
          <div className={styles.editAvatarBlock}>
            <div>
              <Avatar image={avatarUrl} initials="UN" />
            </div>
            <div>
              <input type="file" ref={avatarFileUploadRef} onChange={(e) => uploadFile(e.target?.files?.[0])} hidden />
              <Button onClick={()=>avatarFileUploadRef.current?.click()}>Change avatar</Button>
            </div>
          </div>
          <div className={styles.editAccountField}>
            <div>Email</div>
            <Input readOnly placeholder="john.doe@email.com" defaultValue={user?.email || ''}  ref={userEmailRef} />
          </div>
          <div className={styles.editAccountField}>
            <div>Name</div>
            <Input placeholder="John Doe" defaultValue={user?.name || ''} ref={userNameRef} />
          </div>
          <div className={styles.editAccountButtons}>
            <Button
              style={{ width: 'auto' }}
              onClick={saveAccountChanges}
            >
              Save changes
            </Button>
            <Button
              style={{ width: 'auto' }}
              mode="secondary"
              onClick={() => setEditAccountInfo(false)}
            >
              Cancel
            </Button>
          </div>
        </div>
      )}

      <div className={boardStyles.divider}></div>

      {/* Password  */}
      <div className={boardStyles.subheader}>
        <span className={boardStyles.subheading}>Password change</span>
        <Button
          onClick={() => setChangePassEdit(true)}
          className={boardStyles.editButton}
        >
          Change password
        </Button>
      </div>
      <div>
        Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam sed
        semper nisi, nec scelerisque ante. In porttitor viverra ornare.
      </div>
      {isChangePassEdit && (
        <div>
          <div className={styles.editAccountField}>
            <div>Current password</div>
            <Input ref={userPwdOld} placeholder="type old password" type="password" />
          </div>
          <div className={styles.editAccountField}>
            <div>New password</div>
            <Input ref={userPwdNew1} placeholder="type new password" type="password" />
          </div>
          <div className={styles.editAccountField}>
            <div>Confirm</div>
            <Input ref={userPwdNew2} placeholder="type new password again" type="password" />
          </div>
          <div className={styles.editAccountButtons}>
            <Button
              style={{ width: 'auto' }}
              onClick={saveUserPassword}
            >
              Save new password
            </Button>
            <Button
              style={{ width: 'auto' }}
              onClick={() => setChangePassEdit(false)}
              mode="secondary"
            >
              Cancel
            </Button>
          </div>
        </div>
      )}
      <div className={boardStyles.divider}></div>

      {/* Two factor */}
      <div className={boardStyles.subheader}>
        <span className={boardStyles.subheading}>
          Two-Factor Authentication (2FA)
        </span>
      </div>
      <div>
        Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam sed
        semper nisi, nec scelerisque ante. In porttitor viverra ornare.
      </div>
      <Button mode="secondary" style={{ width: 'auto' }}>
        Disable
      </Button>
      <List>
        <Item>
          <div className={styles.radio}>
            <Radio checked name="two-factor-option" onChange={() => {}}></Radio>
          </div>
          <div
            className={cn(
              boardStyles.box32,
              boardStyles.icon,
              boardStyles.emailIcon
            )}
          ></div>
          <div>
            <div>Email</div>
            <div>john.doe@email.com</div>
          </div>
          <div
            className={cn(
              boardStyles.box24,
              boardStyles.icon,
              boardStyles.iconClose,
              boardStyles.clickable
            )}
            style={{ marginLeft: 'auto' }}
          />
        </Item>
        <Item>
          <div className={styles.radio}>
            <Radio name="two-factor-option"></Radio>
          </div>
          <div
            className={cn(
              boardStyles.box32,
              boardStyles.icon,
              boardStyles.phoneIcon
            )}
          ></div>
          <div>
            <div>SMS</div>
            <div>+7 916 974 74 24</div>
          </div>
          <div
            className={cn(
              boardStyles.box24,
              boardStyles.icon,
              boardStyles.iconClose,
              boardStyles.clickable
            )}
            style={{ marginLeft: 'auto' }}
          />
        </Item>
      </List>
      <Button
        onClick={() => showTwoFactorAuthModal(true)}
        style={{ width: 'auto' }}
      >
        Add another option
      </Button>
      <div className={boardStyles.divider}></div>

      {/* delete */}
      <div className={boardStyles.subheader}>
        <span className={boardStyles.subheading}>Danger zone</span>
      </div>
      <div>
        Deleting your account will remove all personal and shared data, except
        your local saved content. This action is irreversible.
      </div>
      <Button
        mode="primary"
        onClick={() => showRemoveAccModal(true)}
        className={styles.dangerButton}
      >
        Delete account
      </Button>

      {/* modals */}
      <Modal
        style={{ maxWidth: 400 }}
        open={isChangePassModalShown}
        onClose={() => showChangePassModal(false)}
      >
        <div className={styles.modalHeader}>Confirm your current password</div>
        <div className={styles.modalContent}>
          <span>
            We need to confirm that you know the password, otherwise you cannot
            change it.{' '}
            <b>
              If there are 5 attempts, you will be disconnected automatically.
            </b>
          </span>
          <Input placeholder="current password" type="password" />
        </div>
        <div className={styles.modalButtons}>
          <Button mode="secondary" onClick={() => showChangePassModal(false)}>
            Cancel
          </Button>
          <Button
            onClick={() => {
              showChangePassModal(false);
              setChangePassEdit(true);
            }}
          >
            Continue
          </Button>
        </div>
      </Modal>

      <Modal
        style={{ maxWidth: 400 }}
        open={isTwoFactorAuthModalShown}
        onClose={() => showTwoFactorAuthModal(false)}
      >
        <div className={styles.modalHeader}>Authentication</div>
        <div className={styles.modalContent}>
          <div className={styles.listRadio}>
            <div>
              <div className={styles.radio}>
                <Radio checked name="two-factor-option-modal"></Radio>
              </div>
              <div
                className={cn(
                  styles.box32,
                  styles.icon,
                  styles.authenticatorIcon
                )}
              ></div>
              <div>
                <div>Authenticator App</div>
              </div>
            </div>
          </div>
        </div>
        <div className={styles.modalButtons}>
          <Button
            mode="secondary"
            onClick={() => showTwoFactorAuthModal(false)}
          >
            Cancel
          </Button>
          <Button onClick={() => showTwoFactorAuthModal(false)}>Select</Button>
        </div>
      </Modal>

      <Modal
        style={{ maxWidth: 400 }}
        open={isRemoveAccModalShown}
        onClose={() => showRemoveAccModal(false)}
      >
        <div className={styles.modalHeader}>Delete account</div>
        <div className={styles.modalContent}>
          <div>
            Are you sure you want to delete your account? All data, information
            will be removed from our servers and this action is irreversible.
            Type <b>"Delete"</b> then continue by clicking the button
          </div>
          <Input placeholder="Delete" />
        </div>

        <div className={styles.modalButtons}>
          <Button mode="secondary" onClick={() => showRemoveAccModal(false)}>
            Cancel
          </Button>
          <Button
            style={{ width: '100%' }}
            className={styles.dangerButton}
            onClick={() => showRemoveAccModal(false)}
          >
            Delete
          </Button>
        </div>
      </Modal>
    </div>
  );
};
