import {
  createContext,
  forwardRef,
  useCallback,
  useContext,
  useEffect,
  useRef,
  useState,
} from 'react';
import cn from 'classnames';

import { Shadow } from '@/components/ui/Shadow';
import { ModalContextProps, ModalProps } from './types';
import styles from './styles.module.css';

const ModalContext = createContext<ModalContextProps>({
  addOutsideClickListener: () => {},
  closeModal: () => {},
});

export const useModalContext = (): ModalContextProps => {
  return useContext(ModalContext);
};

export const Modal = forwardRef<HTMLDivElement, ModalProps>(
  (
    {
      className,
      shadowClassName,
      testId,
      children,
      open = true,
      durationOpen = 150,
      durationClose = 75,
      onClose,
      closeOnOutsideClick = true,
      ...rest
    },
    ref
  ) => {
    const [isOpen, setIsOpen] = useState(false);
    const [isOpening, setIsOpening] = useState(false);
    const [shadowClassNames, setShadowClassNames] = useState(styles.shadow);
    const shadowRef = useRef<HTMLDivElement>(null);
    const onOutsideClickRef = useRef<(() => void) | null>(null);

    const elementClassNames = cn(styles.modal, className);

    useEffect(() => {
      if (open) {
        setIsOpen(true);
      } else if (shadowRef.current) {
        shadowRef.current.style.opacity = getComputedStyle(
          shadowRef.current
        ).opacity;
      }
      setIsOpening(open);
    }, [open]);

    useEffect(() => {
      if (isOpening) {
        setShadowClassNames(styles.shadow);
      } else if (shadowRef.current) {
        setShadowClassNames(cn(styles.shadow, styles.closing));
      }
    }, [isOpening]);

    const handleAnimationEnd = () => {
      if (!isOpening) {
        setIsOpen(false);
      }
    };

    const closeModal = useCallback(() => {
      if (onClose) {
        onClose();
      }
      if (shadowRef.current) {
        shadowRef.current.style.opacity = getComputedStyle(
          shadowRef.current
        ).opacity;
        setIsOpening(false);
      }
    }, [shadowRef]);

    const onShadowClick = useCallback(() => {
      if (closeOnOutsideClick) {
        closeModal();
      }
      if (onOutsideClickRef.current) {
        onOutsideClickRef.current();
      }
    }, [onOutsideClickRef]);

    const addOutsideClickListener = (handler: () => void) => {
      onOutsideClickRef.current = handler;
    };

    return isOpen ? (
      <Shadow
        onClick={onShadowClick}
        className={cn(shadowClassNames, shadowClassName)}
        onAnimationEnd={handleAnimationEnd}
        style={{
          // @ts-expect-error okok
          '--durationOpen': `${durationOpen}ms`,
          '--durationClose': `${durationClose}ms`,
        }}
        ref={shadowRef}
      >
        <div className={styles.modalContainer}>
          <div
            className={elementClassNames}
            data-testid={testId}
            {...rest}
            ref={ref}
            onClick={(e) => e.stopPropagation()}
          >
            <ModalContext.Provider
              value={{ closeModal, addOutsideClickListener }}
            >
              {children}
            </ModalContext.Provider>
          </div>
        </div>
      </Shadow>
    ) : null;
  }
);
