import { User } from "@/store/types";
import { useAppContext } from "@/utils/useAppContext";
import { useEffect, useState } from "react";
import { useAuth } from "react-oidc-context";

export const useLoadAuth = () => {
  const auth = useAuth();
  const [isLoading, setIsLoading] = useState(auth.isLoading);
  const [isError, setIsError] = useState(Boolean(auth.error));
  const { dispatch } = useAppContext();
  
  useEffect(() => {
    if (auth.activeNavigator && auth.activeNavigator === 'signoutSilent') {
      return;
    } else if (!auth.isLoading && auth.isAuthenticated) {
      setIsLoading(false);
      setIsError(false);

      const payload = {
        ...auth.user?.profile,
        id_token: auth.user?.id_token,
      } as User;

      dispatch({
        type: 'user.update',
        payload
      });

      localStorage.setItem('access_token', auth.user?.access_token || '');
    } else if (auth.error) {
      console.log("auth: error:", auth.error)
      setIsLoading(false);
      setIsError(true);
      dispatch({ type: 'user.reset', });
    } else if (!auth.isLoading) {
      auth.signinRedirect();
      // signInWithRedirect();
      return;
    }
  }, [auth, dispatch]);

  return [isLoading, isError]
};
