import cn from 'classnames';
import styles from './styles.module.css';
import boardStyles from '../board.styles.module.css';
import { Button } from '@/components/ui/Button';
import { useNavigate } from 'react-router';
import { List } from '@/components/ui/List';
import { Item } from '@/components/ui/List/Item';
import { useEffect, useState } from 'react';
import { listAuthorizedDevices } from '@/utils/profile';
import { DeviceType, } from '@aws-sdk/client-cognito-identity-provider';

export const Devices = () => {
  const navigate = useNavigate();

  const [isDevicesLoading, setIsDevicesLoading] = useState<boolean>(true);
  const [authorizedDevices, setAuthorizedDevices] = useState<DeviceType[]>([]);
  console.log("authorizedDevices", authorizedDevices);

  useEffect(() => {
    listAuthorizedDevices().then((devices) => {
      setIsDevicesLoading(false);
      setAuthorizedDevices(devices || [])
    })
  }, [])
  return (
    <div className={styles.block}>
      <div className={boardStyles.subheader}>
        <span className={boardStyles.subheading}>Connected devices</span>
      </div>
      <div>
        Keep an eye on your account's connected devices and IP addresses. If you
        spot any that you don’t recognize, we recommend disconnecting them and
        changing your password immediately.
      </div>
      <div>
        <Button mode="secondary" style={{ width: 'auto' }}>
          Sign out all devices
        </Button>
      </div>

      <br />

      <div className={boardStyles.subheader}>
        <span className={boardStyles.subheading}>
          1 out of 2 active devices
        </span>
      </div>
      {!isDevicesLoading && (
        <List>
          <Item>
            <div
              className={cn(
                boardStyles.box32,
                boardStyles.icon,
                boardStyles.workstationIcon
              )}
            ></div>
            <div>
              <div className={styles.listTitle}>
                Mac OS X · Mexico, Juárez{' '}
                <span className={styles.thisDevice}>This device</span>
              </div>
              <div className={styles.listDesc}>CHROME BROWSER</div>
            </div>
            <div
              className={cn(styles.box24, styles.icon)}
              style={{ marginLeft: 'auto' }}
            />
          </Item>
          <Item>
            <div
              className={cn(
                boardStyles.box32,
                boardStyles.icon,
                boardStyles.phoneIcon
              )}
            ></div>
            <div>
              <div className={styles.listTitle}>Windows 10 · Mexico, Juárez</div>
              <div className={styles.listDesc}>CLIPCLIP CLIENT</div>
            </div>
            <div
              className={cn(
                boardStyles.box24,
                boardStyles.icon,
                boardStyles.iconClose,
                boardStyles.clickable
              )}
              style={{ marginLeft: 'auto' }}
            />
          </Item>
        </List>
      )}

      <div className={boardStyles.divider}></div>

      <div>
        Need more active devices? Upgrade to Premium and expand your
        possibilities!
      </div>
      <div>
        <Button
          mode="primary"
          style={{ width: 'auto' }}
          onClick={() => navigate('/premium')}
        >
          Get Premium
        </Button>
      </div>
    </div>
  );
};
