import { Logo } from '@/components/Logo';
import styles from './styles.module.css';
import { Social } from './Social/Social';
import { Space } from '@/components/ui/Space';
import {
  Route,
  Routes,
  useNavigate,
  useParams,
} from 'react-router';
import cn from 'classnames';
import { useEffect, useState } from 'react';

import { useAuth } from 'react-oidc-context';

import { Account } from '@/pages/Board/Account';
import { Privacy } from '@/pages/Board/Privacy';
import { Devices } from '@/pages/Board/Devices';
import { Connections } from '@/pages/Board/Connections';
import { Premium } from '@/pages/Board/Premium';
import { Billing } from '@/pages/Board/Billing';


import { Avatar } from '@/components/ui/Avatar';
import { useAppContext } from '@/utils';

export const Board = () => {
  const auth = useAuth();
  console.log("user:", auth.user);
  const navigate = useNavigate();
  const [isMenuOpenedMobile, setMenuOpenedMobile] = useState(false);
  const {state, dispatch } = useAppContext();
  console.log("state", state)


  const signOutRedirect = async ({ id_token }: { id_token?: string }) => {
    const clientId = "78nqt15eeh3gof0ksv10q19m5f";
    const logoutUri = "https://clipclip.com";
    const cognitoDomain = "https://clipclip.auth.us-east-1.amazoncognito.com";
    const link = `${cognitoDomain}/logout?client_id=${clientId}&logout_uri=${encodeURIComponent(logoutUri)}&id_token_hint=${id_token}`;
    window.location.href = link;
    sessionStorage.clear();
    dispatch({ type: 'user.reset', });
  };

  useEffect(() => {
    setMenuOpenedMobile(false);
  }, []);

  const { id_token } = auth.user || {};
  const { '*': pageLocation } = useParams();
  const page = pageLocation || 'account';
  console.log(useParams());

  return (
    <div className={styles.layout}>
      <div
        className={cn(styles.sidebar, {
          [styles.menuOpenedMobile]: isMenuOpenedMobile,
        })}
      >
        <Logo className={styles.logotype} />
        <ul className={cn(styles.reset, styles.mobileMargin)}>
          <li className={styles.heading}>
            <span>User settings</span>
          </li>
          <li
            onClick={() => navigate('/account')}
            className={cn({
              [styles.pressed]: page === 'account',
            })}
          >
            <span>Account</span>
          </li>
          <li
            onClick={() => navigate('/privacy')}
            className={cn({
              [styles.pressed]: page === 'privacy',
            })}
          >
            <span>Privacy</span>
          </li>
          {/* <li
            onClick={() => navigate('/devices')}
            className={cn({
              [styles.pressed]: page === 'devices',
            })}
          >
            <span>Devices</span>
          </li> */}
          {/* <li
            onClick={() => navigate('/connections')}
            className={cn({
              [styles.pressed]: page === 'connections',
            })}
          >
            <span>Connections</span>
          </li> */}
          <li
            onClick={() => navigate('/premium')}
            className={cn({
              [styles.pressed]: page === 'premium',
            })}
          >
            <span>Premium</span>
          </li>
          <li
            onClick={() => navigate('/billing')}
            className={cn({
              [styles.pressed]: page === 'billing',
            })}
          >
            <span>Billing Information</span>
          </li>
          <li
            onClick={() =>
              (window.location.href = 'https://clipclip.com/whats-new')
            }
            className={cn({
              [styles.pressed]: page === 'news',
            })}
          >
            <span>What's new?</span>
          </li>
          <li className={styles.divider}></li>
          <li onClick={() => (window.location.href = 'https://clipclip.com')}>
            <span>clipclip.com</span>
          </li>
          <li
            className={cn({
              [styles.pressed]: page === 'close-session',
            })}
            onClick={() => signOutRedirect({ id_token })}
          >
            <span>Close session</span>
          </li>
        </ul>
        <div className={cn(styles.socials, styles.mobileMargin)}>
          <Social />
        </div>
        <div
          className={styles.profile}
          onClick={() => navigate('/account')}
        >
          <Avatar initials="UN" />
          <div>User Name</div>
        </div>
      </div>
      <div className={styles.contentPart}>
        {/* <Space>hello</Space> */}
        <Space
          header={
            <>
              <span
                onClick={() => setMenuOpenedMobile(true)}
                className={styles.headingBackButton}
              ></span>
              <span>{page.charAt(0).toUpperCase() + page.slice(1)}</span>
            </>
          }
        >
          <Routes>
            <Route path="" element={<Account />} />
            <Route path="account" element={<Account />} />
            <Route path="privacy" element={<Privacy />} />
            <Route path="billing" element={<Billing />} />
            <Route path="devices" element={<Devices />} />
            <Route path="connections" element={<Connections />} />
            <Route path="premium" element={<Premium />} />
            <Route path="*" element={<h1>404</h1>} />
          </Routes>
        </Space>
      </div>
    </div>
  );
};
