import cn from 'classnames';
import styles from './styles.module.css';
import { AvatarProps } from './types';

export const Avatar = ({
  initials = '',
  image = '',
  size = 'l',
}: AvatarProps) => {
  const isInitials = initials !== '' && image === '';
  const isPlaceholder = image === '' && !isInitials;
  return (
    <div
      className={cn(styles.avatar, {
        [styles.initials]: isInitials,
        [styles.placeholder]: isPlaceholder,
        [styles.image]: image,
        [styles.m]: size === 'm',
        [styles.l]: size === 'l',
        [styles.xl]: size === 'xl',
      })}
      style={{
        ...(image ? { backgroundImage: `url(${image})` } : {}),
      }}
    >
      {!image && initials}
    </div>
  );
};
