import styles from './styles.module.css';
import boardStyles from '../board.styles.module.css';
import { Switch } from '@/components/ui/Switch';
import { ChangeEventHandler, useState } from 'react';
import { updateUserAttributes } from '@/utils/profile';
import { useAuth } from 'react-oidc-context';
import { useAppContext } from '@/utils';

export const Privacy = () => {
  const { state, } = useAppContext();
  const auth = useAuth();
  const { user } = state;
  const [isShareMitrics, setShareMetrics] = useState(Boolean(user?.['custom:shareMetrics']));

  const onChange: ChangeEventHandler<HTMLInputElement> = async (e) => {
    const value = Boolean(e.target.checked);
    setShareMetrics(value)
    const update = {
      'custom:shareMetrics': value ? "true" : "",
    };
    const result = await updateUserAttributes(update);
    if (result) {
      try {
        await auth.signinSilent(); // This refreshes the tokens
        console.log("User session refreshed:", auth.user);
      } catch (error) {
        console.error("Error refreshing session", error);
        setShareMetrics(isShareMitrics);
      }
    }
  }

  return (
    <div className={styles.block}>
      <div className={boardStyles.subheader}>
        <span className={boardStyles.subheading}>Shared data</span>
      </div>
      <div>
        This is all the analytics data that is being shared during the use of
        the app. Note: no clip content is being shared (even titles), only
        metadata such as type and formats, weight, and other complementary data
        user activity data is being shared as anonymous.
      </div>

      <div className={boardStyles.grayHeader}>Share data for improvements</div>
      <div className={styles.switch}>
        <div>
          By sharing data, you are helping ClipClip improve, you can disable any
          time without a problem
        </div>
        <div>
          <Switch onChange={onChange} checked={isShareMitrics} />
        </div>
      </div>

      <div className={boardStyles.divider}></div>

      {/* <div className={boardStyles.grayHeader}>Download shared data</div>
      <div>
        You can download or delete your data if necessary, don’t worry. Doing so
        will disable Shared data
      </div>
      <div>
        <Button mode="primary">Download data</Button>
      </div> */}
    </div>
  );
};
