import {
  ChangePasswordCommand,
  CognitoIdentityProviderClient,
  ForgetDeviceCommand,
  ListDevicesCommand,
  UpdateDeviceStatusCommand,
  UpdateUserAttributesCommand,
  DeleteUserCommand,
  GetUserCommand,
} from "@aws-sdk/client-cognito-identity-provider";
import { AttributesValues, } from "./types";

/// user attributes

export const updateUserAttributes = async (update: AttributesValues): Promise<AttributesValues | null> => {
  const token = localStorage.getItem('access_token');
  if (!token) {
    return null;
  }
  const client = new CognitoIdentityProviderClient({ region: import.meta.env.PUB_REGION });
  const attKeys = Object.keys(update);
  const command = new UpdateUserAttributesCommand({
    AccessToken: token,
    UserAttributes: attKeys.map((key) => {
      return {
        Name: key,
        Value: update[key],
      };
    }),
  });

  try {
    await client.send(command);
    return update;
  } catch (error) {
    return null;
  }
};

/// delete current user 

export const deleteCurrentUser = async (accessToken: string) => {
  const token = localStorage.getItem('access_token');
  if (!token) {
    console.error("User not authenticated");
    return;
  }

  const client = new CognitoIdentityProviderClient({ region: import.meta.env.PUB_REGION });

  try {
      const command = new DeleteUserCommand({
          AccessToken: accessToken, // Must be a valid access token
      });

      await client.send(command);
      console.log("User deleted successfully.");
  } catch (error) {
      console.error("Error deleting user:", error);
  }
}

/// password

export const updateUserPassword = async (oldPassword: string, newPassword: string) => {
  const token = localStorage.getItem('access_token');
  if (!token) {
    console.error("User not authenticated");
    return;
  }

  const client = new CognitoIdentityProviderClient({ region: import.meta.env.PUB_REGION });

  const command = new ChangePasswordCommand({
    AccessToken: token,
    PreviousPassword: oldPassword,
    ProposedPassword: newPassword,
  });

  try {
    await client.send(command);
    console.log("Password changed successfully!");
    return newPassword;
  } catch (error) {
    console.error("Error changing password:", error);
    return null;
  }
};

/// devices

export const listAuthorizedDevices = async () => {
  const token = localStorage.getItem('access_token');
  if (!token) {
    console.error("User not authenticated");
    return;
  }

  const client = new CognitoIdentityProviderClient({ region: import.meta.env.PUB_REGION });

  const command = new ListDevicesCommand({
    AccessToken: token,
    Limit: 10, // Set the number of devices to retrieve
  });

  try {
    const response = await client.send(command);
    console.log("Authorized devices:", response);
    return response.Devices; // List of devices
  } catch (error) {
    console.error("Error fetching devices:", error);
  }
};

export const forgetDevice = async (deviceKey: string) => {
  const token = localStorage.getItem('access_token');
  if (!token) return;
  const client = new CognitoIdentityProviderClient({ region: import.meta.env.PUB_REGION });

  const command = new ForgetDeviceCommand({
    AccessToken: token,
    DeviceKey: deviceKey,
  });

  try {
    await client.send(command);
    console.log("Device removed:", deviceKey);
    return deviceKey;
  } catch (error) {
    console.error("Error forgetting device:", error);
    return null;
  }
};

export const rememberDevice = async () => {
  const token = localStorage.getItem('access_token');
  if (!token) return;
  const client = new CognitoIdentityProviderClient({ region: import.meta.env.PUB_REGION });

  const command = new UpdateDeviceStatusCommand({
    AccessToken: token,
    DeviceKey: "CURRENT",
    DeviceRememberedStatus: "remembered",
  });

  try {
    await client.send(command);
    console.log("Device successfully remembered!");
    return true;
  } catch (error) {
    console.error("Error remembering device:", error);
    return null;
  }
};

// list mfas 
export async function listMFAOptions() {
  const token = localStorage.getItem('access_token');
  if (!token) return;

  const client = new CognitoIdentityProviderClient({ region: import.meta.env.PUB_REGION });

  const command = new GetUserCommand({ AccessToken: token });


  try {
    const response = await client.send(command);
    console.log("MFA Options:", response.MFAOptions); // SMS, etc.
    return response;
  } catch (error) {
    console.error("Error getting user MFA options", error);
    return null;
  }
}