import React, { forwardRef } from 'react';
import { createPortal } from 'react-dom';

import cn from 'classnames';

import { ShadowProps } from './types';

import styles from './styles.module.css';

export const Shadow = forwardRef<HTMLDivElement, ShadowProps>(
  ({ className = '', children, onClick, ...rest }: ShadowProps, ref) => {
    const elementContainerClassNames = cn(styles.elementContainer, className);
    const handleClick: React.MouseEventHandler = (e) => {
      e.stopPropagation();
      e.preventDefault();
      onClick?.(e);
    };
    const handleKeyboard: React.KeyboardEventHandler = (e) => {
      const key = e.key;
      if (e.target == e.currentTarget && ['Enter', 'Escape'].includes(key)) {
        /** @ts-expect-error keyboard flow support roadmap */
        onClick?.(e);
      }
    };
    return createPortal(
      <div
        className={elementContainerClassNames + ' t-portal'}
        {...rest}
        onClick={handleClick}
        onKeyUp={handleKeyboard}
        tabIndex={onClick ? 0 : undefined}
        aria-label={onClick ? 'Закрыть' : undefined}
        role={onClick ? 'button' : undefined}
        ref={ref}
      >
        {children}
      </div>,
      document.body
    );
  }
);
